import React, { useState, useEffect } from 'react';
import { withStyles, useTheme } from '@material-ui/styles';
import { IconSearch } from '@tabler/icons';
import { useParams, useHistory } from 'react-router-dom';
import {
    IconButton,
    Typography,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    Tooltip,
    CircularProgress
} from '@material-ui/core';
import EditIcon from '@mui/icons-material/Edit';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import AddIcon from '@material-ui/icons/Add';
import GroupIcon from '@material-ui/icons/Group';
import useStyles from '../styles';
import { useDispatch, useSelector } from 'react-redux';
import { useForm } from 'react-hook-form';
import { viewProfileById } from '../../../store/actions/ProfileActions';
import AddProfile from './AddProfileModal';
import AddAddressModal from './AddAddressModal';
import { deleteAddress } from '../../../store/actions/AddressActions';
import useUserRole from '../../../hooks/useUserRole';
import { getModuleTaughtTitle } from '../../../services/batchServices';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);
export default function ProfileList() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'SCHOOL_ADMIN','SUPER_ADMIN'] });
    const history = useHistory();
    const dispatch = useDispatch();
    let { id, role } = useParams();
    const theme = useTheme();
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);
    const [openModal, setModal] = useState(false);

    const [isEditData, setEditData] = useState(null);

    const { viewProfileData } = useSelector((state) => state?.userProfile);
    const [moduleTitle, setModuleTitle] = useState([]);
    const [custId, setCustId] = useState(null);

    const [isLoading, setLoading] = useState(true);
    const { reset } = useForm();

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };
    const handleOpenModal = () => {
        setEditData(null);
        setModal(true);
    };

    const handleCloseModal = () => {
        reset();
        setModal(false);
    };
    const handleEditProfile = (profileData) => {
        setEditData(profileData);
        setModal(true);
    };

    const handleViewAddress = (data) => {
        history.push(`/admin/all-address/${data?.userId}/${data?.id}`);
    };

    useEffect(() => {
        dispatch(viewProfileById(id));
    }, [id]);

    useEffect(() => {
        if (viewProfileData?.userProfile && viewProfileData?.userProfile?.length > 0) {
            const newCustId = viewProfileData?.userProfile[0]?.id;
            setCustId(newCustId);
        }
    }, [viewProfileData]);
    const getModuleTaught = async (id) => {
        try {
            setLoading(true);
            setModuleTitle([]);
            let res = await getModuleTaughtTitle(id);
            setModuleTitle(res);
        } catch (error) {
            console.log(error);
        } finally {
            setLoading(false);
        }
    };
    useEffect(() => {
        if (custId !== null) {
            getModuleTaught(custId);
        }
        setCustId(null);
    }, [custId]);

    return (
        <>
            <div className={classes.titleContainer}>
                <GroupIcon className={classes.pageTitleIcon} />
                <span className={classes.pageTitle}>&nbsp; User Profile </span>
                <div>
                    <span className={classes.pageTitle}>User info: {viewProfileData?.user?.email}</span> <br />
                    <span className={classes.pageTitle}>User role: {viewProfileData?.user?.role}</span>
                </div>
            </div>
            <Paper elevation={5} className={classes.root}>
                <div style={{ display: 'flex', justifyContent: 'end' }}>
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={() => handleOpenModal()}
                        style={{
                            boxShadow: '0 0.5em 0.5em 0.1em #68c2d9',
                            display: viewProfileData?.user?.role === 'STUDENT' ? 'block' : 'none'
                        }}
                    >
                        <AddIcon /> Profile
                    </Button>
                </div>
                <TableContainer className={classes.tableContainer}>
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead className={classes.tableHeaderCell}>
                            <StyledTableRow>
                                <TableCell>Cus ID</TableCell>
                                <TableCell>Full Name</TableCell>
                                <TableCell>Age</TableCell>

                                <TableCell align="center"> Status </TableCell>
                                {(viewProfileData?.user?.role === 'TEACHER'|| role==='teacher') && <TableCell align="center"> Module Taught </TableCell>}

                                <TableCell align="center"> Comments </TableCell>
                                <TableCell align="center"> Action </TableCell>
                            </StyledTableRow>
                        </TableHead>
                        {!viewProfileData?.userProfile ? (
                            <caption style={{ textAlign: 'center' }}>Sorry! No data available</caption>
                        ) : (
                            <TableBody>
                                {viewProfileData?.userProfile?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => (
                                    <StyledTableRow key={row?.id}>
                                        <TableCell>{row?.id || '-'}</TableCell>
                                        <TableCell align="left">{`${row?.firstName} ${row?.lastName}` || '-'}</TableCell>
                                        <TableCell>{row?.age || '-'}</TableCell>
                                        <TableCell align="center">
                                            <Typography
                                                className={classes.status}
                                                style={{
                                                    backgroundColor:
                                                        (row?.status === 'ACTIVE' && theme.palette.success.main) ||
                                                        (row?.status === 'INACTIVE' && theme.palette.warning.dark) ||
                                                        (row?.status === 'ACTIVE_PENDING' && theme.palette.warning.dark) ||
                                                        (row?.status === 'ACTIVE_APPROVED' && theme.palette.success.main) ||
                                                        (row?.status === 'ACTIVE_REJECTED' && theme.palette.error.dark)
                                                }}
                                            >
                                                {row?.status}
                                            </Typography>
                                        </TableCell>
                                        {(viewProfileData?.user?.role === 'TEACHER' || role==='teacher') && <TableCell align="right" style={{ textAlign: '-webkit-center' }}>
                                            {isLoading ? (
                                                <CircularProgress
                                                    style={{
                                                        display: 'flex',
                                                        alignItems: 'center',
                                                        justifyContent: 'center',
                                                        height: '100%',
                                                        width: '100%',
                                                        width: '20px',
                                                        height: '20px'
                                                    }}
                                                />
                                            ) : (
                                                 (
                                                    <select>
                                                        {moduleTitle?.map((title, index) => (
                                                            <option key={index} value={title?.title}>
                                                                {title?.title}
                                                            </option>
                                                        ))}
                                                    </select>
                                                )
                                            )}
                                        </TableCell>}
                                        <TableCell align="right" style={{ textAlign: '-webkit-center' }}>
                                            <TableCell align="center">{row?.adminComment ? 'Yes' : 'No'}</TableCell>
                                        </TableCell>
                                        <TableCell align="center">
                                            <Tooltip title="Edit profile">
                                                <IconButton style={{ cursor: 'pointer' }} onClick={() => handleEditProfile(row)}>
                                                    <EditIcon color="primary" />
                                                </IconButton>
                                            </Tooltip>
                                            <Tooltip title="View address">
                                                <IconButton style={{ cursor: 'pointer' }} onClick={() => handleViewAddress(row)}>
                                                    <RemoveRedEyeIcon color="primary" />
                                                </IconButton>
                                            </Tooltip>
                                        </TableCell>
                                    </StyledTableRow>
                                ))}
                            </TableBody>
                        )}
                    </Table>
                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[25, 50, 100]}
                    component="div"
                    count={viewProfileData?.userProfile?.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onPageChange={handleChangePage}
                    onRowsPerPageChange={handleChangeRowsPerPage}
                    sx={{
                        '.MuiTablePagination-displayedRows': {
                            marginBottom: 0
                        },
                        '.MuiTablePagination-selectLabel': {
                            marginBottom: 0
                        }
                    }}
                />
            </Paper>
            {openModal && <AddProfile open={openModal} onClose={handleCloseModal} isEditData={isEditData} userId={id} />}
        </>
    );
}
