import { http } from '../../../utils/http';
import {
    ADDRESS_START,
    ADDRESS_SUCCESS,
    ADDRESS_ERROR,
    UPDATE_ADDRESS_SUCCESS,
    UPDATE_ADDRESS_START,
    UPDATE_ADDRESS_ERROR,
    VIEW_ALL_ADDRESS_ERROR,
    VIEW_ALL_ADDRESS_START,
    VIEW_ALL_ADDRESS_SUCCESS
} from '../../constants';
import { setMessage } from '../MessageActions';

const addAddressStart = () => {
    return { type: ADDRESS_START };
};
const addAddressSuccess = (payload) => {
    return { type: ADDRESS_SUCCESS, payload };
};
const addAddressError = (error) => {
    return { type: ADDRESS_ERROR, error };
};
const updateAddressStart = () => {
    return { type: UPDATE_ADDRESS_START };
};
const updateAddressSuccess = (payload) => {
    return { type: UPDATE_ADDRESS_SUCCESS, payload };
};
const updateAddressError = (error) => {
    return { type: UPDATE_ADDRESS_ERROR, error };
};
const viewAddressStart = () => {
    return { type: VIEW_ALL_ADDRESS_START };
};
const viewAddressSuccess = (payload) => {
    return { type: VIEW_ALL_ADDRESS_SUCCESS, payload };
};
const viewAddressError = (error) => {
    return { type: VIEW_ALL_ADDRESS_ERROR, error };
};

const viewAddress =
    (userId, profileId, pageSize = 10) =>
    (dispatch, getState) => {
        dispatch(viewAddressStart());
        try {
            http(
                process.env.REACT_APP_ROOT_URL,
                'GET',
                `users-service/users/${userId}/profiles/${profileId}/addresses?pageNumber=1&pageSize=${pageSize}`,
                true
            )
                .then((res) => {
                    const payload = {
                        viewAddressData: res
                    };
                    dispatch(viewAddressSuccess(payload));
                    dispatch(setMessage(res?.message));
                })
                .catch((err) => {
                    dispatch(viewAddressError(err));
                    dispatch(setMessage(err?.message));
                });
        } catch (error) {
            dispatch(viewAddressError('Something went wrong'));
        }
    };
const addAddress = (payload, userId, profileId, successCB) => (dispatch, getState) => {
    dispatch(addAddressStart());
    try {
        http(process.env.REACT_APP_ROOT_URL, 'POST', `users-service/users/${userId}/profiles/${profileId}/addresses`, payload, true)
            .then((res) => {
                dispatch(addAddressSuccess(payload));
                dispatch(setMessage(res?.message));
                successCB && successCB(res);
            })
            .catch((err) => {
                dispatch(addAddressError(err));
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        dispatch(addAddressError('Something went wrong'));
    }
};
const updateAddress = (payload, userId, profileId, addressId, successCB) => (dispatch, getState) => {
    dispatch(updateAddressStart());
    try {
        http(
            process.env.REACT_APP_ROOT_URL,
            'PUT',
            `users-service/users/${userId}/profiles/${profileId}/addresses/${addressId}`,
            payload,
            true
        )
            .then((res) => {
                dispatch(updateAddressSuccess(payload));
                dispatch(setMessage(res?.message));
                successCB && successCB(res);
            })
            .catch((err) => {
                dispatch(updateAddressError(err));
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        dispatch(updateAddressError('Something went wrong'));
    }
};
const deleteAddress = (userId, addressId, successCB) => (dispatch, getState) => {
    try {
        http(process.env.REACT_APP_ROOT_URL, 'DELETE', `users-service/users/${userId}/addresses/${addressId}`, true)
            .then((res) => {
                dispatch(setMessage('Address Deleted Successfully!'));
                successCB && successCB(res);
            })
            .catch((err) => {
                dispatch(setMessage(err?.message));
            });
    } catch (error) {
        dispatch(setMessage(error?.message));
    }
};

export { addAddress, updateAddress, deleteAddress, viewAddress };
