import React, { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { useSelector } from 'react-redux';
import CloseIcon from '@material-ui/icons/Close';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { FormProvider, RHFTextField, RHFCheckbox, RHFSelect } from '../../../components/hook-form';
import useStyles from '../styles';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography, Grid, Link, Divider, TextField } from '@material-ui/core';
import { useDispatch } from 'react-redux';
import { AddProfiles, viewProfileById } from '../../../store/actions/ProfileActions';
import { getAllSchool, getSchoolList } from '../../../store/actions/SchoolActions';
import { updateProfile } from '../../../store/actions/AddUserActions';
import useUserRole from '../../../hooks/useUserRole';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250
        }
    }
};

const addProfileSchema = yup.object({
    firstName: yup.string('Enter first name').required('First name is required'),
    lastName: yup.string('Enter last name').required('Last name is required'),
    status: yup.string('Status is required').required('Status is required'),
    dob: yup.string('Enter Date Of Birth').required('Date Of Birth is required')
});

const STATUS_ITEM = [
    { id: 0, status: 'SELECT STATUS' },
    { id: 1, status: 'ACTIVE' },
    { id: 2, status: 'INACTIVE' }
];
const STATUS_ITEMS = [
    { id: 0, status: 'SELECT STATUS' },
    { id: 1, status: 'ACTIVE_PENDING' },
    { id: 2, status: 'ACTIVE_APPROVED' },
    { id: 3, status: 'ACTIVE_REJECTED' }
];

export default function AddProfile(props) {
    const { open, onClose, isEditData, userId, isUserRole } = props;
    console.log(isEditData);
    // console.log('---User Role---', isUserRole);
    const dispatch = useDispatch();
    const classes = useStyles();
    let { role } = useParams();
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);

    const { schoolList } = useSelector((state) => state?.school);

    const initialValues = {
        firstName: isEditData?.firstName || '',
        lastName: isEditData?.lastName || '',
        default: isEditData?.default || false,
        schoolId: isEditData?.schoolId || '',
        classAndSection: isEditData?.classAndSection || '',
        status: isEditData?.status,
        dob: isEditData?.dob,
        comments: isEditData?.adminComment
    };

    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(addProfileSchema),
        defaultValues: initialValues
    });

    const { reset, handleSubmit, watch } = methods;
    const hide = watch('schoolId');

    useEffect(() => {
        if (!isEditData) {
            reset(initialValues);
        }
    }, [isEditData]);

    const onSubmit = (data) => {
        const payload = {
            firstName: data?.firstName,
            lastName: data?.lastName,
            default: data?.default,
            schoolId: data?.schoolId,
            status: data?.status
        };

        let modifiedPayload = {};
        if (data?.schoolId === '') {
            modifiedPayload = {
                firstName: data?.firstName,
                lastName: data?.lastName,
                default: data?.default,
                status: data?.status,
                dob: data?.dob,
                adminComment: data?.comments
            };
        } else {
            modifiedPayload = {
                firstName: data?.firstName,
                lastName: data?.lastName,
                default: data?.default,
                schoolId: data?.schoolId,
                classAndSection: data?.classAndSection,
                status: data?.status,
                dob: data?.dob,
                adminComment: data?.comments
            };
        }

        if (isEditData) {
            dispatch(
                updateProfile(modifiedPayload, isEditData?.userId, isEditData?.id, () => {
                    dispatch(viewProfileById(isEditData?.userId));
                    reset(initialValues);
                    onClose();
                })
            );
        } else {
            dispatch(
                AddProfiles(modifiedPayload, userId, () => {
                    dispatch(viewProfileById(userId));
                    reset();
                    onClose();
                })
            );
        }
    };

    // const userRoleName = (role) => {
    //     switch (role) {
    //         case 'ADMIN':
    //             return 'Admin';
    //         case 'SCHOOL_ADMIN':
    //             return 'School-Admin';
    //         case 'TEACHER':
    //             return 'Teacher';
    //         case 'STUDENT':
    //             return 'Student';

    //         default:
    //             return role;
    //     }
    // };

    useEffect(() => {
        // dispatch(getAllSchool());
        dispatch(getSchoolList());
    }, []);

    return (
        <div>
            <Dialog open={open} onClose={onClose} maxWidth="xs" classes={{ paper: classes.dialog }}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}>{isEditData ? 'Update' : 'Add'} Profile</Typography>
                        <Link to="#" onClick={onClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                    <DialogContent>
                        <Grid container spacing={2}>
                            <Grid item xs={12} sm={12}>
                                <label className={classes.label}>
                                    First Name <span className={classes.mandatory}>*</span>
                                </label>
                                <RHFTextField name="firstName" placeholder="First name" className={classes.loginInput} autoFocus />
                            </Grid>
                            <Grid item xs={12} sm={12}>
                                <label className={classes.label}>
                                    Last Name <span className={classes.mandatory}>*</span>
                                </label>
                                <RHFTextField name="lastName" placeholder="Last name" className={classes.loginInput} />
                            </Grid>
                            <Grid item xs={12} sm={12}>
                                <label className={classes.label}>
                                    Date Of Birth <span className={classes.mandatory}>*</span>
                                </label>
                                <RHFTextField type="date" name="dob" placeholder="Date Of Birth" className={classes.loginInput} />
                            </Grid>
                            {role == 'student' && (
                                <Grid item xs={12} sm={12}>
                                    <label className={classes.label}> School Name </label>
                                    {/* <RHFTextField name="schoolId" placeholder="schoolId" className={classes.loginInput} /> */}
                                    <RHFSelect name="schoolId" label="">
                                        <option value=""> Select School </option>
                                        {schoolList?.map((item, index) => (
                                            <option key={index} value={item?.id}>
                                                {item?.nameBranch}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                </Grid>
                            )}

                            {role == 'school-admin' && (
                                <Grid item xs={12} sm={12}>
                                    <label className={classes.label}> School Name </label>
                                    {/* <RHFTextField name="schoolId" placeholder="schoolId" className={classes.loginInput} /> */}
                                    <RHFSelect name="schoolId" label="">
                                        <option value=""> Select School </option>
                                        {schoolList?.map((item, index) => (
                                            <option key={index} value={item?.id}>
                                                {item?.nameBranch}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                </Grid>
                            )}

                            {hide !== '' && hide !== 'Select a School' && role == 'student' && (
                                <Grid item xs={12}>
                                    {/* {loggedUserProfile?.role !== 'SCHOOL_ADMIN' && ( */}
                                    <RHFTextField name="classAndSection" placeholder="Enter your class/section" />
                                    {/* )} */}
                                </Grid>
                            )}

                            <Grid item xs={12}>
                                <label>
                                    Status <span className={classes.mandatory}>*</span>
                                </label>
                                {/* {loggedUserProfile?.role === 'SCHOOL_ADMIN' && (
                                    <> */}
                                {hide == '' && (
                                    <RHFSelect name="status" label="">
                                        {STATUS_ITEM?.map((item, index) => (
                                            <option key={index} value={item?.status}>
                                                {item?.status}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                )}

                                {hide !== '' && role == 'school-admin' && (
                                    <RHFSelect name="status" label="">
                                        {STATUS_ITEM?.map((item, index) => (
                                            <option key={index} value={item?.status}>
                                                {item?.status}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                )}
                                {/* </>
                                )} */}
                                {/* `${comment} Date:-${new Date()}` */}
                                {hide !== '' && role == 'student' && (
                                    <>
                                        <RHFSelect name="status" label="">
                                            {STATUS_ITEMS?.map((item, index) => (
                                                <option key={index} value={item?.status}>
                                                    {item?.status}
                                                </option>
                                            ))}
                                        </RHFSelect>
                                    </>
                                )}
                            </Grid>
                            {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                                <Grid item xs={12}>
                                    <label>Comments</label>
                                    <RHFTextField name="comments" multiline rows={4} />
                                </Grid>
                            )}

                            <Grid item xs={12} sm={12}>
                                <RHFCheckbox name="default" label="Default" />
                            </Grid>
                        </Grid>
                    </DialogContent>
                    <Divider style={{ marginTop: 15 }} />
                    <DialogActions className={classes.dialogAction}>
                        <Button variant="outlined" color="primary" className={classes.buttonCancel} onClick={onClose}>
                            Close
                        </Button>
                        <Button variant="contained" color="primary" type="submit" className={classes.buttonSubmit}>
                            {isEditData ? 'Update' : 'Add'}
                        </Button>
                    </DialogActions>
                </FormProvider>
            </Dialog>
        </div>
    );
}
